import Vue from 'vue'
import VueI18n from 'vue-i18n'
import langEn from "@/assets/en.json"

Vue.use(VueI18n)
 
const i18n = new VueI18n({
  locale: 'en',//默认使用简体中文
  fallbackLocale: 'en',//如果当前语种不存在时，默认设置当前语种
  messages: {
    'en': {...langEn},//英文-en
  }
})

export default i18n


